$blue: #109DFF
$yellow: #FEDC00
$gray: #ECEEF0

+font("MuseoSansCyrl-100", museosanscyrl_100)
+font("MuseoSansCyrl-300", museosanscyrl_300)
+font("MuseoSansCyrl-500", museosanscyrl_500)
+font("MuseoSansCyrl-700", museosanscyrl_700)
+font("PTSerif-Regular", ptf55f)
+font("PTSerif-Bold", ptf75f)

=light
  font-family: "MuseoSansCyrl-100", Helvetica, Arial, sans-serif
  font-weight: normal

=reg
  font-family: "MuseoSansCyrl-300", Helvetica, Arial, sans-serif
  font-weight: normal

=bolder
  font-family: "MuseoSansCyrl-500", Helvetica, Arial, sans-serif
  font-weight: normal

=bold
  font-family: "MuseoSansCyrl-700", Helvetica, Arial, sans-serif
  font-weight: normal

=ptr
  font-family: "PTSerif-Regular", Helvetica, Arial, sans-serif
  font-weight: normal

=ptb
  font-family: "PTSerif-Bold", Helvetica, Arial, sans-serif
  font-weight: normal


body
  position: relative
  z-index: 0
  min-width: 320px
  font-size: 14px
  line-height: 1.2
  background: #3B4952
  color: #3B4952
  +bolder
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  &.is-menu-open
    overflow: hidden

a
  color: $blue

.out
  position: relative
  z-index: 2
  min-height: 100%
  overflow: hidden
  transition: transform 0.4s ease
  background: #FFF
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5)
  .is-menu-open &
    height: 100%
    transform: translateX(87.5%)


#screen-container
  width: 320px
  margin: 20px auto
  min-height: 100%
  box-shadow: 0 0 3px 0 lightblue
  position: relative
  z-index: 0
  overflow: hidden
  &.is-switched
    width: 335px
    height: 568px
    min-height: 0
    overflow-x: hidden
    overflow-y: auto
    -webkit-overflow-scrolling: touch

.page-wrapper
  padding: 20px 20px 150px
  position: relative
  z-index: 1
  background: #FFF
  min-height: 100%

.book-page-wrapper
  padding: 20px 20px 60px
  background: #FFF

.page-overlay
  position: absolute
  z-index: 98
  top: 0
  left: 0
  right: 0
  bottom: 0
  background: #40484D
  transition: all 0.2s linear
  // visibility: hidden
  opacity: 0.1
  display: none
  &.is-visible
    opacity: 0.9
    // visibility: visible
  .is-menu-open &
    z-index: 101
    &.is-visible
      opacity: 0.6
